:root {
    --primary: #4f46e5;
    --primary-dark: #4338ca;
    --primary-light: #6366f1;
    --accent: #10b981;
    --accent-dark: #059669;
    --danger: #ef4444;
    --warning: #f59e0b;
    --success: #10b981;
    --text-primary: #1f2937;
    --text-secondary: #4b5563;
    --bg-light: #f9fafb;
    --bg-dark: #111827;
    --border-light: #e5e7eb;
    --border-dark: #d1d5db;
    --glass-bg: rgba(255, 255, 255, 0.8);
    --glass-border: rgba(255, 255, 255, 0.2);
    --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
    --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
    line-height: 1.5;
    color: var(--text-primary);
    background-color: var(--bg-light);
}

.derma-app {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.app-header {
    background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
    color: white;
    padding: 2rem 1rem;
    text-align: center;
    box-shadow: var(--shadow-md);
}

.header-content h1 {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
}

.tagline {
    font-size: 1.1rem;
    opacity: 0.9;
}

.app-container {
    flex: 1;
    padding: 2rem 1rem;
    max-width: 1200px;
    margin: 0 auto;
    width: 100%;
}

.classifier-card {
    background: var(--glass-bg);
    backdrop-filter: blur(10px);
    border-radius: 1rem;
    border: 1px solid var(--glass-border);
    box-shadow: var(--shadow-xl);
    overflow: hidden;
    transition: all 0.3s ease;
}

.glassmorphism {
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
}

.card-header {
    padding: 1.5rem;
    text-align: center;
    border-bottom: 1px solid var(--border-light);
}

.header-icon {
    width: 48px;
    height: 48px;
    margin: 0 auto 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(79, 70, 229, 0.1);
    border-radius: 50%;
    color: var(--primary);
}

.card-header h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.subheader {
    color: var(--text-secondary);
    font-size: 0.95rem;
}

.error-message {
    background-color: #fee2e2;
    color: #b91c1c;
    padding: 1rem;
    margin: 1rem;
    border-radius: 0.5rem;
    border-left: 4px solid #ef4444;
}

.loading-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    color: var(--text-secondary);
}

.pulse-loader {
    width: 3rem;
    height: 3rem;
    border: 0.5rem solid #e5e7eb;
    border-top-color: var(--primary);
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 1rem;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.upload-area {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.dropzone {
    border: 2px dashed var(--border-dark);
    border-radius: 0.75rem;
    transition: all 0.2s ease;
    margin: 1rem;
}

.dropzone:hover {
    border-color: var(--primary-light);
    background-color: rgba(79, 70, 229, 0.05);
}

.upload-cta {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.file-types {
    font-size: 0.875rem;
    color: var(--text-secondary);
}

.primary-btn {
    background-color: var(--primary);
    color: white;
    border: none;
    padding: 0.75rem 1.5rem;
    border-radius: 0.5rem;
    font-weight: 500;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    transition: all 0.2s ease;
    box-shadow: var(--shadow-sm);
}

.primary-btn:hover {
    background-color: var(--primary-dark);
    transform: translateY(-1px);
    box-shadow: var(--shadow-md);
}

.primary-btn:active {
    transform: translateY(0);
}

.primary-btn.accent {
    background-color: var(--accent);
}

.primary-btn.accent:hover {
    background-color: var(--accent-dark);
}

.secondary-btn {
    background-color: white;
    color: var(--primary);
    border: 1px solid var(--primary-light);
    padding: 0.75rem 1.5rem;
    border-radius: 0.5rem;
    font-weight: 500;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    transition: all 0.2s ease;
}

.secondary-btn:hover {
    background-color: rgba(79, 70, 229, 0.05);
    transform: translateY(-1px);
    box-shadow: var(--shadow-sm);
}

.icon-upload,
.icon-analyze,
.icon-hospital {
    width: 1.25rem;
    height: 1.25rem;
}

.spinner {
    width: 1.25rem;
    height: 1.25rem;
    border: 3px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top-color: white;
    animation: spin 1s ease-in-out infinite;
}

.spinner.small {
    width: 1rem;
    height: 1rem;
    border-width: 2px;
}

.image-preview-container {
    padding: 0 1.5rem 1.5rem;
}

.image-frame {
    position: relative;
    border-radius: 0.75rem;
    overflow: hidden;
    box-shadow: var(--shadow-md);
}

.preview-image {
    display: block;
    width: 100%;
    max-height: 400px;
    object-fit: contain;
    background-color: #f3f4f6;
}

.image-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
    padding: 1rem;
    color: white;
    font-size: 0.875rem;
    opacity: 0;
    transition: opacity 0.2s ease;
}

.image-frame:hover .image-overlay {
    opacity: 1;
}

.zoom-hint {
    display: flex;
    align-items: center;
    gap: 0.25rem;
}

.results-panel {
    padding: 1.5rem;
    border-top: 1px solid var(--border-light);
}

.results-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
}

.result-title {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.result-title h3 {
    font-size: 1.25rem;
    font-weight: 600;
}

.confidence-tag {
    font-size: 0.875rem;
    font-weight: 500;
    padding: 0.25rem 0.75rem;
    border-radius: 9999px;
    background-color: #e0e7ff;
    color: var(--primary);
}

.confidence-tag.normal {
    background-color: #d1fae5;
    color: var(--success);
}

.confidence-tag.benign {
    background-color: #fef3c7;
    color: var(--warning);
}

.confidence-tag.malignant {
    background-color: #fee2e2;
    color: var(--danger);
}

.result-meta {
    font-size: 0.875rem;
    color: var(--text-secondary);
}
.action-section {
    margin-top: 2rem;
    padding-top: 1.5rem;
    border-top: 1px solid var(--border-light);
}
.diagnosis-card {
    margin-bottom: 2rem;
}

.condition-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 1.125rem;
    font-weight: 600;
    padding: 0.75rem 1.25rem;
    border-radius: 0.5rem;
    margin-bottom: 1rem;
}

.condition-badge.normal {
    background-color: #d1fae5;
    color: var(--success);
}

.condition-badge.benign {
    background-color: #fef3c7;
    color: var(--warning);
}

.condition-badge.malignant {
    background-color: #fee2e2;
    color: var(--danger);
}

.badge-icon {
    width: 1.5rem;
    height: 1.5rem;
}

.probability-meter {
    margin-top: 1rem;
}

.meter-labels {
    display: flex;
    justify-content: space-between;
    font-size: 0.75rem;
    color: var(--text-secondary);
}



.hospital-search-section {
    margin-top: 2rem;
    padding: 1.5rem;
    background: rgba(249, 250, 251, 0.8);
    border-radius: 0.75rem;
    border: 1px solid var(--border-light);
}

.search-header {
    margin-bottom: 1.5rem;
}

.search-subheader {
    color: var(--text-secondary);
    font-size: 0.95rem;
}

.location-input-group {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.input-wrapper {
    position: relative;
    display: flex;
    align-items: center;
}

.input-icon {
    position: absolute;
    left: 1rem;
    color: var(--text-secondary);
    width: 1.25rem;
    height: 1.25rem;
}

.location-input {
    width: 100%;
    padding: 0.75rem 1rem 0.75rem 3rem;
    border: 1px solid var(--border-dark);
    border-radius: 0.5rem;
    font-size: 1rem;
    transition: all 0.2s ease;
}

.location-input:focus {
    outline: none;
    border-color: var(--primary);
    box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.search-hospital-btn {
    align-self: flex-start;
}





.hospital-results {
    margin-top: 2rem;
    padding: 1.5rem;
    background: white;
    border-radius: 0.75rem;
    border: 1px solid var(--border-light);
    box-shadow: var(--shadow-sm);
}

.results-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.results-count {
    font-size: 0.875rem;
    color: var(--text-secondary);
}

.hospital-list {
    line-height: 1.8;
}

.hospital-list p {
    margin-bottom: 1.5rem;
}

.hospital-list strong {
    color: var(--text-primary);
}



.location-info {
    background-color: #f8fafc;
    border-radius: 0.5rem;
    padding: 1rem;
    margin: 1.5rem 0;
    border-left: 4px solid var(--primary);
}

.location-info h5 {
    font-size: 0.875rem;
    color: var(--text-secondary);
    margin-bottom: 0.25rem;
}

.location-info p {
    font-weight: 500;
}

.location-disclaimer {
    font-size: 0.75rem;
    color: var(--text-secondary);
    margin-top: 0.5rem;
    text-align: center;
}

.refresh-btn.small {
    padding: 0.5rem 1rem;
    font-size: 0.875rem;
    margin-top: 1rem;
}

.icon-refresh {
    width: 1rem;
    height: 1rem;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.refresh-btn:not(:hover) .icon-refresh {
    animation: none;
}



.probability-meter {
    margin-top: 1.5rem;
}

.meter-labels {
    display: flex;
    justify-content: space-between;
    font-size: 0.75rem;
    color: var(--text-secondary);
    margin-bottom: 0.25rem;
}

.meter-track {
    height: 8px;
    background-color: #e5e7eb;
    /* Light gray track */
    border-radius: 4px;
    overflow: hidden;
}

.meter-fill {
    height: 100%;
    border-radius: 4px;
    background-color: var(--primary);
    /* Default color */
    transition: width 0.3s ease;
}

/* Condition-specific colors */
.meter-fill.normal {
    background-color: var(--success);
    /* Green for normal */
}

.meter-fill.benign {
    background-color: var(--warning);
    /* Yellow for benign */
}

.meter-fill.malignant {
    background-color: var(--danger);
    /* Red for malignant */
}